.gradationLine {
  z-index: 5;
  height: 2px;
  width: 100%;
  background: -webkit-linear-gradient(left, #4395ff , #E8E8E8); /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(right, #4395ff, #E8E8E8); /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(right, #4395ff, #E8E8E8); /* Firefox 3.6 - 15 */
  background: linear-gradient(to right, #4395ff , #E8E8E8); /* 标准的语法 */
}
